@import 'style';

.PostsList {
  & {
    width: 100%;

    display: flex;
    flex-direction: column;
  
    text-align: left;

    .Title {
      margin-bottom: 16px;
    }

    .PostsList__container {
      display: flex;
      flex-direction: column;
      gap: 25px;

      @media (min-width: 1024px) {
        gap: 40px;
      }
    }
  }
}
